@import 'variables';

.navbarwrapper{
    position: relative;
    display: flex;
    align-items: center;
    flex-direction: column;
    max-height: 31vh;
    width: 100vw;
}



.logo{
    @media screen and (max-width: $medium) {
        height: 6vh;
    }
    cursor: pointer;
    
    height: 15vh;
    // max-height: 15vh;
    width: 70%;
    margin-top: 5px;
    min-width: 280px;
    max-width: 800px;
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
   }

.sociallinkwrapper{
    display: flex;
    align-items: center;
    position: absolute;
    right:1%;
    top:2%;
    gap: 5px;

    a:nth-child(-n+2){
        transform: scale(1.2);
        margin-right: 2px;
    }
}  

.sociallink{
    color: black;
    cursor: pointer;
}

.branchwrapper{
    max-height: 60%;
    min-width: 280px;
    // width: 50%;
    max-width: 800px;
    display: grid;
    grid-auto-flow: column;
    grid-auto-columns: 1fr;
    .branch{
        text-align: center;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: flex-end;
        margin: 3px;
        .branchlogo{
            width: 100%;
            height: 15vh;
            background-position: center;
            background-size: contain;
            background-repeat: no-repeat;
            cursor: pointer;
        }
        
        .branchname{
            font-size: 1.2rem;
            bottom: 1%;
            min-width: 9.5rem;
        }
    }
}

.kidscarelogo{
    // transform: scale(1.02);
    filter: drop-shadow(5px -5px 10px #cfd5d8 ) drop-shadow(-5px -5px 10px #cfd5d8) saturate(130%);
    transition: transform 1s, filter 1s;
}
.cosmetologielogo{
    // transform: scale(1.02);
    filter: drop-shadow(5px -5px 10px #bee8cf) drop-shadow(-5px -5px 10px #bee8cf) saturate(130%);
    transition: transform 1s, filter 1s;
}
.bodyconceptlogo{
    // transform: scale(1.02);
    filter: drop-shadow(5px -5px 10px $ocherglow) drop-shadow(-5px -5px 10px $ocherglow) saturate(130%);
    transition: transform 1s, filter 1s;
}
.curamedixlogo{
    // transform: scale(1.02);
    filter: drop-shadow(5px -5px 10px #bedae8) drop-shadow(-5px -5px 10px #bedae8) saturate(130%);
    transition: transform 1s, filter 1s;
}

.navbarline{
    display: flex;
    justify-content: center;
    margin-top: 5px;
    width: 100%;
    background-color: black;
    .nav-buttons{
        display: flex;
        justify-content: space-around;
        min-width: 660px;
        width: 60%;
        max-width: 800px;
        color: white;
        font-size: 1.2rem;
        div{
            cursor: pointer;

        }
    }
    .quadrat{
        margin-right: auto;
        height: 2rem;
        width: 2rem;
        background-size: contain;
        background-position: center;
        background-repeat: no-repeat;
        cursor: pointer;
        filter: invert(100%);
    }
    .phone{
        filter: invert(100%);
    }

    @keyframes wiggle {
        0% { transform: translate(1px, 1px) rotate(0deg); }
        10% { transform: translate(-1px, -2px) rotate(-1deg); }
        20% { transform: translate(-3px, 0px) rotate(1deg); }
        30% { transform: translate(3px, 2px) rotate(0deg); }
        40% { transform: translate(1px, -1px) rotate(1deg); }
        50% { transform: translate(-1px, 2px) rotate(-1deg); }
        60% { transform: translate(-3px, 1px) rotate(0deg); }
        70% { transform: translate(3px, 1px) rotate(-1deg); }
        80% { transform: translate(-1px, -1px) rotate(1deg); }
        90% { transform: translate(1px, 2px) rotate(0deg); }
        100% { transform: translate(1px, -2px) rotate(-1deg); }
    }

    .hoverphone{
        animation: wiggle 1s;
    }
}
    